
import { defineComponent } from "vue";
import { prettyNumber } from "@/utils";
import type { ShoppingCart } from "@/models";

const component = defineComponent({
    name: "PurchaseDetails",
    props: {
        report: {
            type: Object as () => ShoppingCart,
            required: true
        }
    },
    setup() {
        return { prettyNumber };
    }
});

export default component;
